.wrap {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.3rem;
}

.button {
    width: 140px;
    height: 45px;
    /*font-family: 'Roboto', sans-serif;*/
    font-family: Noto Sans KR;
    font-weight: 400;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    /*font-weight: 500;*/
    color: #000;
    background-color: #fff;

    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(20, 20, 25, 0.15);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
}

.button:hover {
    background-color: #2EE59D;
    box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
    color: #fff;
    transform: translateY(-7px);
}
